<template>
  <div class="home">
    <el-form
      :model="ruleForm"
      label-position="top"
      ref="ruleForm"
      class="demo-ruleForm"
    >
      <el-form-item :label="ruleForm.resource.key">
        <el-radio-group v-model="ruleForm.resource.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.gettime.key">
        <el-date-picker
          type="date"
          placeholder="选择患病时间"
          v-model="ruleForm.gettime.value"
          style="width: 50%"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd"
        >
          ></el-date-picker
        >
      </el-form-item>
      <el-form-item :label="ruleForm.reason.key">
        <el-radio-group v-model="ruleForm.reason.value">
          <el-radio label="缓"></el-radio>
          <el-radio label="急"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label="ruleForm.types.key">
        <el-checkbox-group v-model="ruleForm.types.value">
          <el-checkbox label="不洁饮食" name="types"></el-checkbox>
          <el-checkbox label="输注血液制品" name="types"></el-checkbox>
          <el-checkbox label="野外作业 " name="types"></el-checkbox>
          <el-checkbox label="动物咬伤史" name="types"></el-checkbox>
          <el-checkbox
            label="近期疫区旅居并与传染病患者接触史"
            name="types"
          ></el-checkbox>
          <el-checkbox label="疫水接触史" name="types"></el-checkbox>
          <el-checkbox label="以上均无 " name="types"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item :label="ruleForm.tanxing.key">
        <el-checkbox-group v-model="ruleForm.tanxing.value">
          <el-checkbox label="发热" name="tanxing"></el-checkbox>
          <el-checkbox label="皮疹  " name="tanxing"></el-checkbox>
          <el-checkbox label="头痛" name="tanxing"></el-checkbox>
          <el-checkbox label="腹痛 " name="tanxing"></el-checkbox>
          <el-checkbox label="黄疸" name="tanxing"></el-checkbox>
          <el-checkbox label="休克" name="tanxing"></el-checkbox>
          <el-checkbox label="昏迷" name="tanxing"></el-checkbox>
          <el-checkbox label="以上均无" name="tanxing"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item :label="ruleForm.more.key">
        <el-radio-group v-model="ruleForm.more.value">
          <el-radio label="有"></el-radio>
          <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item style="text-align: center">
        <el-button type="primary" @click="submitForm('ruleForm')"
          >打印</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  data() {
    return {
      ruleForm: {
        resource: {
          key: "一.有无感染科病史",
          value: "",
        },
        gettime: {
          key: "二.患病时间",
          value: "",
        },
        reason: {
          key: "三.起病缓急",
          value: "",
        },
        types: {
          key: "四.有无以下诱因及可疑感染史（可多选）",
          value: [],
        },
        tanxing: {
          key: "五.有无以下症状（可多选）",
          value: [],
        },
        more: {
          key: "六.家族及周围人群中有无类似疾病、带菌或病毒性肝炎病原携带者",
          value: "",
        },
      },
    };
  },
  methods: {
    submitForm() {
      console.log(this.ruleForm);
      window.android.printer(JSON.stringify(this.ruleForm));
    },
  },
};
</script>
